'use client';

import { useState, useEffect } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { 
  Plus, 
  X, 
  Phone, 
  Mail, 
  MessageCircle, 
  Facebook, 
  Github, 
  Linkedin,
  ArrowUp,
  Home,
  User,
  Briefcase,
  Settings
} from 'lucide-react';

interface SpeedDialAction {
  icon: React.ComponentType<any>;
  label: string;
  onClick: () => void;
  color: string;
  href?: string;
}

export default function SpeedDial() {
  const [isOpen, setIsOpen] = useState(false);
  const [showScrollTop, setShowScrollTop] = useState(false);

  // Theo dõi scroll để hiển thị nút scroll to top
  useEffect(() => {
    const handleScroll = () => {
      setShowScrollTop(window.scrollY > 300);
    };

    window.addEventListener('scroll', handleScroll);
    return () => window.removeEventListener('scroll', handleScroll);
  }, []);

  const scrollToTop = () => {
    window.scrollTo({ top: 0, behavior: 'smooth' });
  };

  const scrollToSection = (sectionId: string) => {
    const element = document.getElementById(sectionId);
    if (element) {
      element.scrollIntoView({ behavior: 'smooth' });
    }
    setIsOpen(false);
  };

  const openLink = (url: string) => {
    window.open(url, '_blank', 'noopener,noreferrer');
    setIsOpen(false);
  };

  const actions: SpeedDialAction[] = [
    {
      icon: Home,
      label: 'Trang Chủ',
      onClick: () => scrollToSection('hero'),
      color: 'bg-blue-500 hover:bg-blue-600'
    },
    {
      icon: User,
      label: 'Về Tôi',
      onClick: () => scrollToSection('about'),
      color: 'bg-green-500 hover:bg-green-600'
    },
    {
      icon: Briefcase,
      label: 'Kinh Nghiệm',
      onClick: () => scrollToSection('experience'),
      color: 'bg-purple-500 hover:bg-purple-600'
    },
    {
      icon: Settings,
      label: 'Dịch Vụ',
      onClick: () => scrollToSection('services'),
      color: 'bg-orange-500 hover:bg-orange-600'
    },
    {
      icon: Phone,
      label: 'Gọi Điện',
      onClick: () => window.open('tel:+84123456789'),
      color: 'bg-emerald-500 hover:bg-emerald-600'
    },
    {
      icon: Mail,
      label: 'Email',
      onClick: () => window.open('mailto:phuhuy@example.com'),
      color: 'bg-red-500 hover:bg-red-600'
    },
    {
      icon: MessageCircle,
      label: 'Zalo',
      onClick: () => openLink('https://zalo.me/0123456789'),
      color: 'bg-blue-600 hover:bg-blue-700'
    },
    {
      icon: Facebook,
      label: 'Facebook',
      onClick: () => openLink('https://facebook.com/phuhuy'),
      color: 'bg-blue-700 hover:bg-blue-800'
    },
    {
      icon: Github,
      label: 'GitHub',
      onClick: () => openLink('https://github.com/phuhuy'),
      color: 'bg-gray-700 hover:bg-gray-800'
    },
    {
      icon: Linkedin,
      label: 'LinkedIn',
      onClick: () => openLink('https://linkedin.com/in/phuhuy'),
      color: 'bg-blue-800 hover:bg-blue-900'
    }
  ];

  return (
    <>
      {/* Speed Dial Container */}
      <div className="fixed bottom-6 right-6 z-50">
        {/* Actions */}
        <AnimatePresence>
          {isOpen && (
            <motion.div
              initial={{ opacity: 0 }}
              animate={{ opacity: 1 }}
              exit={{ opacity: 0 }}
              className="absolute bottom-16 right-0 space-y-3"
            >
              {actions.map((action, index) => (
                <motion.div
                  key={action.label}
                  initial={{ 
                    opacity: 0, 
                    scale: 0,
                    x: 20,
                    y: 20
                  }}
                  animate={{ 
                    opacity: 1, 
                    scale: 1,
                    x: 0,
                    y: 0
                  }}
                  exit={{ 
                    opacity: 0, 
                    scale: 0,
                    x: 20,
                    y: 20
                  }}
                  transition={{ 
                    delay: index * 0.05,
                    type: "spring",
                    stiffness: 300,
                    damping: 20
                  }}
                  className="flex items-center gap-3"
                >
                  {/* Label */}
                  <motion.div
                    whileHover={{ scale: 1.05 }}
                    className="glass-card backdrop-blur-lg border border-white/10 px-3 py-2 rounded-lg"
                  >
                    <span className="text-sm font-medium text-white whitespace-nowrap">
                      {action.label}
                    </span>
                  </motion.div>

                  {/* Action Button */}
                  <motion.button
                    whileHover={{ scale: 1.1 }}
                    whileTap={{ scale: 0.95 }}
                    onClick={action.onClick}
                    className={`w-12 h-12 rounded-full ${action.color} text-white shadow-lg flex items-center justify-center transition-all duration-200`}
                  >
                    <action.icon className="h-5 w-5" />
                  </motion.button>
                </motion.div>
              ))}
            </motion.div>
          )}
        </AnimatePresence>

        {/* Main Speed Dial Button */}
        <motion.button
          whileHover={{ scale: 1.1 }}
          whileTap={{ scale: 0.95 }}
          onClick={() => setIsOpen(!isOpen)}
          className="w-14 h-14 bg-gradient-to-r from-red-500 to-red-600 text-white rounded-full shadow-lg flex items-center justify-center transition-all duration-300 hover:shadow-xl"
        >
          <motion.div
            animate={{ rotate: isOpen ? 45 : 0 }}
            transition={{ duration: 0.2 }}
          >
            <Plus className="h-6 w-6" />
          </motion.div>
        </motion.button>
      </div>

      {/* Scroll to Top Button */}
      <AnimatePresence>
        {showScrollTop && (
          <motion.button
            initial={{ opacity: 0, scale: 0 }}
            animate={{ opacity: 1, scale: 1 }}
            exit={{ opacity: 0, scale: 0 }}
            whileHover={{ scale: 1.1 }}
            whileTap={{ scale: 0.95 }}
            onClick={scrollToTop}
            className="fixed bottom-6 left-6 w-12 h-12 bg-gradient-to-r from-gray-700 to-gray-800 text-white rounded-full shadow-lg flex items-center justify-center transition-all duration-300 hover:shadow-xl z-50"
          >
            <ArrowUp className="h-5 w-5" />
          </motion.button>
        )}
      </AnimatePresence>

      {/* Backdrop */}
      <AnimatePresence>
        {isOpen && (
          <motion.div
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            exit={{ opacity: 0 }}
            onClick={() => setIsOpen(false)}
            className="fixed inset-0 bg-black/20 backdrop-blur-sm z-40"
          />
        )}
      </AnimatePresence>
    </>
  );
}
